blockquote {
  background: #f5f6fa;
  margin: 0 auto;
  padding: 5px 10px;
  border-left: 3px solid #8ac5f3;
  border-radius: 4px;
}

/* width */
#site-nav::-webkit-scrollbar {
  width: 5px;
}

/* Track */
#site-nav::-webkit-scrollbar-track {
  /* background: #f1f1f1; */
  background:transparent;
}
 
/* Handle */
#site-nav::-webkit-scrollbar-thumb {
  background: transparent;
  border-radius:50px;
}
.side-bar:hover #site-nav::-webkit-scrollbar-thumb{
	background: #D6D6D6;
}
/* Handle on hover */
#site-nav::-webkit-scrollbar-thumb:hover {
  background: #D6D6D6; 
}
img[data-action="zoom"] {
  cursor: pointer;
  cursor: -webkit-zoom-in;
  cursor: -moz-zoom-in;
}
.main-content-wrap img,.zoom-img,
.zoom-img-wrap {
  position: relative;
  z-index: 666;
  -webkit-transition: opacity 200ms;
  -o-transition: opacity 200ms;
  transition: opacity 200ms;
  transition: 200ms;
}
.main-content-wrap img, img.zoom-img {
  cursor: pointer;
  cursor: -webkit-zoom-out;
  cursor: -moz-zoom-out;
  transition: 200ms;
}
.zoom-overlay {
  z-index: 420;
  background: #fff;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  pointer-events: none;
  filter: "alpha(opacity=0)";
  opacity: 0;
  -webkit-transition: opacity 200ms;
  -o-transition: opacity 200ms;
  transition: opacity 200ms;
  transition: 200ms;
}
.zoom-overlay-open .zoom-overlay {
  filter: "alpha(opacity=100)";
  opacity: 1;
}
.zoom-overlay-open,
.zoom-overlay-transitioning {
  cursor: default;
}

